"use client";

import React, { useMemo, useState } from "react";
import MemberModal, {
  MemberFormValue,
  MemberStatus,
} from "@/components/box/MemberModal";
import ConfirmDeleteModal from "@/components/box/ConfirmDeleteModal";

export type Member = {
  id: string;
  name: string;
  email?: string;
  phone?: string;
  status: MemberStatus;
  membershipExpiresAt?: string; // YYYY-MM-DD
  notes?: string;
};

type Props = {
  initialMembers?: Member[];
};

const badgeClass = (status: MemberStatus) => {
  switch (status) {
    case "active":
      return "inline-flex items-center rounded-full bg-success-50 px-2.5 py-0.5 text-xs font-medium text-success-700 dark:bg-success-500/10 dark:text-success-400";
    case "expired":
      return "inline-flex items-center rounded-full bg-warning-50 px-2.5 py-0.5 text-xs font-medium text-warning-700 dark:bg-warning-500/10 dark:text-warning-400";
    case "inactive":
    default:
      return "inline-flex items-center rounded-full bg-gray-100 px-2.5 py-0.5 text-xs font-medium text-gray-700 dark:bg-white/[0.06] dark:text-gray-300";
  }
};

export default function MembersTable({ initialMembers }: Props) {
  const [query, setQuery] = useState("");

  const [members, setMembers] = useState<Member[]>(
    initialMembers ?? [
      {
        id: "m_1",
        name: "Juan P√©rez",
        email: "juan@example.com",
        phone: "+34 600 111 222",
        status: "active",
        membershipExpiresAt: "2026-02-01",
        notes: "Hombro derecho (vigilar)",
      },
      {
        id: "m_2",
        name: "Mar√≠a L√≥pez",
        email: "maria@example.com",
        phone: "+34 600 333 444",
        status: "expired",
        membershipExpiresAt: "2025-12-15",
      },
      {
        id: "m_3",
        name: "Pedro Garc√≠a",
        status: "inactive",
      },
    ]
  );

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ Modales ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */

  const [modalOpen, setModalOpen] = useState(false);
  const [modalMode, setModalMode] = useState<"create" | "edit">("create");
  const [editingMember, setEditingMember] = useState<Member | null>(null);

  const [deleteOpen, setDeleteOpen] = useState(false);
  const [memberToDelete, setMemberToDelete] = useState<Member | null>(null);

  const openCreate = () => {
    setModalMode("create");
    setEditingMember(null);
    setModalOpen(true);
  };

  const openEdit = (m: Member) => {
    setModalMode("edit");
    setEditingMember(m);
    setModalOpen(true);
  };

  const openDelete = (m: Member) => {
    setMemberToDelete(m);
    setDeleteOpen(true);
  };

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ Acciones ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */

  const toggleActive = (id: string) => {
    setMembers((prev) =>
      prev.map((m) =>
        m.id === id
          ? {
              ...m,
              status: m.status === "inactive" ? "active" : "inactive",
            }
          : m
      )
    );
  };

  const onDelete = (id: string) => {
    setMembers((prev) => prev.filter((m) => m.id !== id));
  };

  const onSubmitModal = (val: MemberFormValue) => {
    if (modalMode === "create") {
      setMembers((prev) => [
        {
          id: `m_${Date.now()}`,
          name: val.name,
          email: val.email,
          phone: val.phone,
          status: val.status,
          membershipExpiresAt: val.membershipExpiresAt,
          notes: val.notes,
        },
        ...prev,
      ]);
      return;
    }

    if (modalMode === "edit" && editingMember) {
      setMembers((prev) =>
        prev.map((m) =>
          m.id === editingMember.id
            ? {
                ...m,
                name: val.name,
                email: val.email,
                phone: val.phone,
                status: val.status,
                membershipExpiresAt: val.membershipExpiresAt,
                notes: val.notes,
              }
            : m
        )
      );
    }
  };

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ Filtro ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */

  const filtered = useMemo(() => {
    const q = query.trim().toLowerCase();
    if (!q) return members;
    return members.filter((m) =>
      `${m.name} ${m.email ?? ""} ${m.phone ?? ""} ${m.status}`
        .toLowerCase()
        .includes(q)
    );
  }, [members, query]);

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ Render ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */

  return (
    <div>
      {/* Modales */}
      <MemberModal
        isOpen={modalOpen}
        onClose={() => setModalOpen(false)}
        mode={modalMode}
        initialValue={editingMember}
        onSubmit={onSubmitModal}
      />

      <ConfirmDeleteModal
        isOpen={deleteOpen}
        onClose={() => setDeleteOpen(false)}
        title={
          memberToDelete
            ? `¬øEliminar a ${memberToDelete.name}?`
            : "¬øEliminar miembro?"
        }
        onConfirm={() => {
          if (memberToDelete) onDelete(memberToDelete.id);
        }}
      />

      {/* Top bar */}
      <div className="mb-4 flex flex-col gap-3 sm:flex-row sm:items-center sm:justify-between">
        <input
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder="Buscar miembro‚Ä¶"
          className="h-11 w-full rounded-lg border border-gray-200 bg-transparent px-4 text-sm text-gray-800 dark:border-gray-800 dark:bg-white/[0.03] dark:text-white/90 sm:max-w-[360px]"
        />

        <button
          onClick={openCreate}
          className="inline-flex items-center justify-center gap-2 rounded-lg bg-brand-500 px-4 py-2.5 text-sm font-medium text-white hover:bg-brand-600"
        >
          + A√±adir miembro
        </button>
      </div>

      {/* Tabla */}
      <div className="overflow-hidden rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
        <div className="max-w-full overflow-x-auto">
          <table className="w-full table-auto">
            <thead>
              <tr className="border-b border-gray-200 dark:border-gray-800">
                <th className="px-6 py-3 text-left text-sm text-gray-500 dark:text-gray-400">
                  Miembro
                </th>
                <th className="px-6 py-3 text-left text-sm text-gray-500 dark:text-gray-400">
                  Estado
                </th>
                <th className="px-6 py-3 text-left text-sm text-gray-500 dark:text-gray-400">
                  Vence
                </th>
                <th className="px-6 py-3 text-left text-sm text-gray-500 dark:text-gray-400">
                  Contacto
                </th>
                <th className="px-6 py-3 text-center text-sm text-gray-500 dark:text-gray-400">
                  Acci√≥n
                </th>
              </tr>
            </thead>

            <tbody className="divide-y divide-gray-100 dark:divide-gray-800">
              {filtered.map((m) => (
                <tr key={m.id}>
                  <td className="px-6 py-[18px]">
                    <div className="font-medium text-gray-800 dark:text-white/90">
                      {m.name}
                    </div>
                    {m.notes && (
                      <div className="text-xs text-gray-500 dark:text-gray-400">
                        {m.notes}
                      </div>
                    )}
                  </td>

                  <td className="px-6 py-[18px]">
                    <span className={badgeClass(m.status)}>
                      {m.status === "active"
                        ? "Activo"
                        : m.status === "expired"
                        ? "Vencido"
                        : "Inactivo"}
                    </span>
                  </td>

                  <td className="px-6 py-[18px] text-sm text-gray-700 dark:text-gray-300">
                    {m.membershipExpiresAt ?? "‚Äî"}
                  </td>

                  <td className="px-6 py-[18px]">
                    <div className="text-sm text-gray-700 dark:text-gray-300">
                      {m.email ?? "‚Äî"}
                    </div>
                    <div className="text-sm text-gray-500 dark:text-gray-400">
                      {m.phone ?? ""}
                    </div>
                  </td>

                  <td className="px-6 py-[18px] text-center">
                    <div className="flex items-center justify-center gap-2">
                      <button onClick={() => openEdit(m)}>‚úèÔ∏è</button>
                      <button onClick={() => toggleActive(m.id)}>
                        {m.status === "inactive" ? "‚ñ∂Ô∏è" : "‚è∏Ô∏è"}
                      </button>
                      <button onClick={() => openDelete(m)}>üóëÔ∏è</button>
                    </div>
                  </td>
                </tr>
              ))}

              {filtered.length === 0 && (
                <tr>
                  <td
                    colSpan={5}
                    className="px-6 py-8 text-center text-sm text-gray-500 dark:text-gray-400"
                  >
                    No hay resultados
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
